<template>
  <v-tab-item>
    <v-slide-group class="mb-4">
      <v-slide-item
        v-for="item in items"
        :key="item.title"
      >
        <v-hover
          v-slot:default="{ hover }"
          close-delay="300"
        >
          <v-card
            color="#24272b"
            class="d-flex justify-center align-center ma-4"
            style="cursor: pointer;"
            height="58"
            width="196"
            flat
          >
            <div class="text-truncate grey--text px-3">
              {{ item.title }}
            </div>

            <v-expand-transition>
              <div
                v-if="hover"
                class="d-flex justify-center align-center blue lighten-1 text-h6 v-card--reveal"
                style="height: 100%;"
              >
                {{ item.value }}
              </div>
            </v-expand-transition>
          </v-card>
        </v-hover>
      </v-slide-item>
    </v-slide-group>

    <v-row class="mb-6">
      <v-col
        cols="12"
        md="6"
      >
        <v-card flat>
          <v-data-table
            :headers="headersGuns"
            :items="guns"
            disable-pagination
            disable-sort
            fixed-header
            hide-default-footer
            height="320"
          >
            <template v-slot:[`item.gun`]="{ item }">
              <v-tooltip top>
                <template v-slot:activator="{ on, attrs }">
                  <img
                    :src="item.url"
                    :alt="item.name"
                    class="ma-4"
                    style="filter: invert(100%); cursor: default;"
                    v-bind="attrs"
                    v-on="on"
                  >
                </template>
                <span>{{ item.name }}</span>
              </v-tooltip>
            </template>
          </v-data-table>
        </v-card>
      </v-col>
      <v-col
        cols="12"
        md="6"
      >
        <v-card flat>
          <v-data-table
            :headers="headersMaps"
            :items="maps"
            disable-pagination
            disable-sort
            fixed-header
            hide-default-footer
            height="320"
          >
            <template v-slot:[`item.map`]="{ item }">
              <div class="d-flex align-center">
                <v-tooltip top>
                  <template v-slot:activator="{ on, attrs }">
                    <img
                      :src="require(`@/assets/maps/${item.name}.jpg`)"
                      :alt="item.name"
                      height="48"
                      width="90"
                      class="rounded ma-4"
                      style="cursor: default;"
                      v-bind="attrs"
                      v-on="on"
                    >
                  </template>
                  <span>{{ item.name }}</span>
                </v-tooltip>
                <span class="ml-1">{{ item.name }}</span>
              </div>
            </template>
          </v-data-table>
        </v-card>
      </v-col>
    </v-row>

    <div class="text-center text-h6 mb-6">
      Кол-во пройденных карт
    </div>

    <v-row>
      <v-col
        v-for="mode of modes"
        :key="mode.name"
      >
        <v-card
          flat
          :img="require(`@/assets/modes/${mode.name}.png`)"
          class="d-flex justify-space-between align-center px-3 py-2"
        >
          <div class="pa-4 text-h6">
            {{ mode.name }}
          </div>
          <v-card-title>{{ mode.count }}</v-card-title>
        </v-card>
      </v-col>
    </v-row>
  </v-tab-item>
</template>

<script>
export default {
  data() {
    return {
      items: [
        { title: 'Первых убийств', value: 1305 },
        { title: 'Убийств прострелом', value: 921 },
        { title: 'Убийств без прицела', value: 277 },
        { title: 'Убийств на бегу', value: 2152 },
        { title: 'Убийств слепым', value: 58 },
