<template>
  <v-row
    align="center"
    style="pointer-events: none;"
  >
    <v-hover
      v-slot:default="{ hover }"
      style="pointer-events: auto;"
    >
      <v-avatar
        size="128"
        class="mr-2"
      >
        <img
          src="@/assets/avatar.jpg"
          alt="fen1x"
        >

        <v-fade-transition>
          <div
            v-if="hover"
            class="d-flex justify-center align-center black v-avatar--reveal"
            style="height: 100%;"
          >
            <img
              src="@/assets/social/steam-icon.webp"
              alt="Steam"
              class="social-1 mr-1"
            >
            <img
              src="@/assets/social/faceit-icon.webp"
              alt="Faceit"
              class="social-2 ml-1"
            >
          </div>
        </v-fade-transition>
      </v-avatar>
    </v-hover>

    <v-col>
      <v-card
        color="transparent"
        flat
        class="pa-2 py-2"
      >
        <v-card-title>
          <span class="nickname text-h4 font-weight-bold mr-2">fen1x</span>
          <v-avatar
            size="20"
            class="mt-1"
          >
            <img
              data-tippy-placement="bottom"
              src="https://cybershoke.net/storage/images/flags/ru.svg"
            >
          </v-avatar>
        </v-card-title>
        <v-card-subtitle>Был(а) в игре - 1 час назад</v-card-subtitle>
      </v-card>
      <div
        class="progress"
        style="pointer-events: auto;"
      >
        <v-tooltip left>
          <template v-slot:activator="{ on, attrs }">
            <v-progress-linear
              color="#ffb510"
              background-color="#0a1016"
              height="8"
              rounded
              class="ml-6"
              style="cursor: help;"
              :value="value"
              v-bind="attrs"
              v-on="on"
            />
          </template>
          <span>Опыт: 60800 XP</span>
        </v-tooltip>

        <div
          v-if="!$vuetify.breakpoint.smAndDown"
          class="progress__icon"
        />
      </div>
    </v-col>
  </v-row>
</template>

<script>
export default {
  data() {
    return {
      value: 0,
    };
  },
  mounted() {
    setTimeout(() => {
      this.value = 80;
    }, 400);
  },
};
</script>

<style lang="sass" scoped>
.v-avatar--reveal
  position: absolute
  bottom: 0
  width: 100%
  opacity: 0.8

.social-1, .social-2
  width: 22px
  height: 22px
  filter: invert(100%)
  cursor: pointer
  transition: all 0.3s
  &:hover
    opacity: 0.7

.nickname
  background: linear-gradient(45deg, #eda531 33%, #f08b33 66%, #f8c333)
  color: gold
  -webkit-text-fill-color: transparent
  -webkit-background-clip: text

.progress
  position: relative
  &__icon
    position: absolute
    top: -10px
    right: -62px
    width: 28px
    height: 28px
    background: url(https://cybershoke.net/storage/images/ranks/11.svg) no-repeat center / cover
  &::before
    content: '76000 XP'
    position: absolute
    top: 10px
    right: 10px
    color: rgba(255, 255, 255, 0.7)
    font-size: 12px
    font-weight: 400
</style>
